
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}



.nav {
    display: flex;
    gap: 20px;
}

.navLink {
    color: #2B2B2B;
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;
    transition: 300ms;
}

.navLink:hover {
    color: #FF4B4E;
    text-decoration: underline;
}


.main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2d2b3e;
    height: 300px;
}

p.title {
    color: #47009E;
    text-transform: uppercase;
    font-weight: 700;
    margin: 0;
    font-size: 40px;
}

p.desc {
    color: #2B2B2B;
    font-size: 20px;
    margin: 15px 0;
    font-weight: 500;
}

.navBtn {
    margin: 50px 0;
}

.navLinkBtn {
    background-color: #FF4B4E;
    text-decoration: none;
    font-size: 20px;
    color: #fff;
    border-radius: 10px;
    transition: 300ms;
    padding: 10px 20px;
}

.navLinkBtn:hover {
    padding: 10px 30px;
    color: #350303;
}
.light {
    position: absolute;
    z-index: -1;
    right: 150px;
}