/* General Styles */
.questionsContainer {
  padding: 20px;
}

.tools {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.filterTools {
  display: flex;
  gap: 20px;
  align-items: center;
}



/* deleteBtn - кнопка удаления */
.questionsTable button.deleteBtn {
  padding: 8px 15px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.questionsTable button.deleteBtn:hover {
  background-color: #c82333;
}

.questionsTable button.deleteBtn:focus {
  outline: none;
  border-color: #c82333;
}

/* editBtn - кнопка редактирования */
.questionsTable button.editBtn {
  padding: 8px 15px;
  background-color: #505f70;
  color: white;
  border: none;
  margin-top: 5px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.questionsTable button.editBtn:hover {
  background-color: #36404d;
}

.questionsTable button.editBtn:focus {
  outline: none;
  border-color: #0056b3;
}


.selectContainer {
  width: 200px;
}

select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 10px 15px;
  border: none;
  color: black;
  cursor: pointer;
  transition: 300ms;
  border-radius: 4px;
  font-size: 15px;
  text-align: justify;
  font-family: "Nunito";
}

button:disabled {
  background-color: #ccc;
}

.questionsTable button:hover:not(:disabled) {
  background-color: #c0c9d6;
  
}



.addTool {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.addBtn {
  padding: 10px 15px;
  background-color: #28a745;
  color: white;
  font-size: 14px;
  border-radius: 4px;
}

.addBtn:hover {
  background-color: #218838;
}

.errorMessage {
  color: red;
  font-weight: bold;
  margin-top: 10px;
}

.loadingMessage {
  color: #6c757d;
  font-weight: bold;
  margin-top: 10px;
}

.questionsTable {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
  margin-top: 20px;
}

.questionsTable th, .questionsTable td {
  padding: 12px;
  text-align: left;
  border: 1px solid #ccc;
}

.questionsTable th {
  background-color: #f8f9fa;
}

.questionsTable td button {
  border: none;
  background: none;
  color: #2f2f2f;
  cursor: pointer;
}


.pagination {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
}

.pagination button {
  padding: 8px 12px;
  background-color: #f8f9fa;
  border: 1px solid #ccc;
  cursor: pointer;
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.pagination select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modalContent {
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  width: 50%;
  text-align: justify;
  max-height: 80vh;  /* Ограничение по высоте */
  overflow-y: auto;  /* Включение вертикальной прокрутки */
}

.modalContent h3 {
  margin-bottom: 20px;
}

.modalContent p {
  margin-bottom: 10px;
}

.modalContent ul {
  margin-left: 20px;
  list-style: none;
  padding: 0;
}

.modalContent ul li {
  margin-bottom: 5px;
}

.modalContent button {
  margin-top: 20px;
  text-align: center;
  background-color: #dc3545;
}

.closeBtn {
  color: white;
}

.modalBtn {
  text-align: center;
}

.modalContent button:hover {
  background-color: #c82333;
}

/* Styling for active filters */
.selectContainer select:focus,
button:focus {
  outline: none;
  border-color: #0056b3;
}


.filterForm {
  display: flex;
  gap: 10px;
}

.filterBtn {
  background-color: #505f70;
  color: white;
  transition: 300ms;
}


.filterBtn:hover {
  background-color: #36404d;

}

/* Модальное окно */
.addQuestionModal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Содержимое модального окна */
.addQuestionContent {
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  width: 50%;
  text-align: center;
  max-height: 80vh;
  overflow-y: auto;
}

/* Заголовок */
.addQuestionContent h3 {
  margin-bottom: 20px;
}

/* Форма */
.addQuestionForm {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

/* Поля ввода */
.inputField {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

/* Контейнер для кнопок */
.buttonsContainer {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

/* Кнопка "Сохранить" */
.saveBtn {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
  border: none;
}

.saveBtn:hover {
  background-color: #218838;
}

/* Кнопка "Отмена" */
.cancelBtn {
  padding: 10px 20px;
  background-color: #dc3545;
  color: white;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
  border: none;
}

.cancelBtn:hover {
  background-color: #c82333;
}
